import { Component } from '@angular/core';
import { IonicPage, App, NavController, Loading, LoadingController, AlertController, Events, PopoverController } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';
import { LoginPage } from '../../pages/login/login';
import { PedirCitaPage } from '../../pages/pedir-cita/pedir-cita';
import { Calendar } from '@ionic-native/calendar';
import { PopoverPage } from '../../pages/popover/popover';
import { DomSanitizer } from '@angular/platform-browser';


@IonicPage()
@Component({
  selector: 'page-recall',
  templateUrl: 'recall.html',
})
export class RecallPage {

	loading: 		Loading; 		// Variable de tipo Loading para mostrar el ProgressBar cuando la pgina est cargando.
	recall 			= Array();
	infoR 			= {fechaFutura: false};
	botonPedirCita	= {name: 'PEDIR CITA DE HIGIENE', svg: 'citas', openPage: 'PedirCita', tipo: 'page', gradiente: '', class: 'active'};
	
	constructor(private app : App, private domSanitizer: DomSanitizer, private calendar: Calendar, public popoverCtrl: PopoverController, public events: Events, public restProvider: RestProvider, private loadingCtrl: LoadingController, private alertCtrl: AlertController, public navCtrl: NavController) {
		this.showLoading();
		this.getRecall();
		this.events.publish("user:logged");	
	}
	
	openPage(page, tipo) {
		if(tipo == "page"){
			if(page == "PedirCita")
				this.app.getRootNav().push(PedirCitaPage);
		}else if(tipo == "web"){
			window.open(page, '_system', 'location=yes');
		}		
	}
	
	/**
	* 	Funcin que aade al calendario una cita.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
	addEvent(timestampINI,timestampFIN) {
		this.showLoading("Aadiendo al calendario")		
		
		let dateINI 	= new Date(parseInt(timestampINI));
		let dateFIN 	= new Date(parseInt(timestampFIN));
		let titulo 		= 'Cita en clnica dental Ferrus & Bratos';
		let direccion 	= 'C/ Caleruega 67 3 Planta. 28033 Madrid';
		
		let options = { calendarId: 1, calendarName: "Clnica Ferrus & Bratos", url: 'http://clinicaferrusbratos.com', firstReminderMinutes: 15 };
	 
		this.calendar.createEventInteractivelyWithOptions(titulo, direccion, '', dateINI, dateFIN, options).then(res => {
			this.loading.dismiss();
		}, err => {
		  this.loading.dismiss();
		});
	}
	
	/**
	* 	Funcin que muestra un pop-up para gestionar la cita.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
	presentPopover(myEvent, fecha, hora) {
		let popover = this.popoverCtrl.create(PopoverPage, {fecha:fecha, hora:hora});
		popover.present({
			ev: myEvent
		});
	}

	/**
	* 	Funcin que muestra una alerta para confirmar o
	*	anular la accin requerida.
	*
	* 	@param String Accion de gestin de la cita (Anulada, Cambio o Confirmada)
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	presentConfirm(action,fechaDecimal, horaDecimal) {
		let alert = this.alertCtrl.create({
			title: 'Confirmacin requerida',
			message: 'Quieres ' + action + ' la cita?',
			buttons: [{ text: 'CANCELAR', role: 'cancel'},{
					text: action,
					handler: () => {
						this.showLoading("Gestionando la cita ...");
						this.gestionarCita(action, fechaDecimal, horaDecimal);							
					}
				}
			]
		});
		alert.present();
	}

	/**
	* 	Funcin que muestra gestiona la cita haciendo
	*	uso de la API del sistema
	*
	* 	@param String Tipo de gestin de la cita (Anulada, Cambio o Confirmada)
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	gestionarCita(tipo, fechaDecimal, horaDecimal) {		
		var textoAlert = "";
		
		if(tipo == "anular")
			textoAlert = "Hemos anulado tu cita.";
		else if(tipo == "cambiar")
			textoAlert = "Nos pondremos en contacto contigo para cambiar la cita.";
		else if(tipo == "confirmar")
			textoAlert = "Hemos confirmado tu cita.";
		
		this.restProvider.gestionarCita(tipo, fechaDecimal, horaDecimal).then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				this.showError("Informacin",textoAlert);	
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.events.publish("user:Unauthorized");				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");
			}			
		}).catch(e => {
			this.showError("ERROR","Hubo un error al gestionar tu cita.");	
		});		
	}

	/**
	* 	Funcin que obtiene las higienes y recall
	*	del paciente
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	getRecall(){
		this.restProvider.getRecall().then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				this.recall = data['data']['data'];
				this.infoR 	= data['data'];				
				this.loading.dismiss();
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.navCtrl.setRoot(LoginPage);				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");
				console.log(data['message']);
			}			
		}).catch(e => {
			this.loading.dismiss();
			console.log(e);
		});		
	}
	
	/**
	* 	Funcin que muestra el ProgressBar cuando alguna accin
	*	se est ejecutando en primer plano.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	showLoading(txt = 'Cargando informacin...') {
		this.loading = this.loadingCtrl.create({
			content: txt,			
			dismissOnPageChange: false
		});
		this.loading.present();
	}
	
	/**
	* 	Funcin que muestra una alerta con el titulo
	*	y el texto pasado por parmetro.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	showError(title,text) {
		this.loading.dismiss();
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: text,
			buttons: ['OK']
		});
		alert.present();
	}
 

}



