import { Component,ViewChild } from '@angular/core';
import { App, NavController, Loading, ToastController, LoadingController, AlertController, Events } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';

import { TabHigienesPage } from '../tab-higienes/tab-higienes';
import { PedirCitaPage } from '../pedir-cita/pedir-cita';

// Para aceptar HTML desde la API
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'page-list',
	templateUrl: 'ConsultarCitas.html'
})

export class ConsultarCitas {

	loading: 	Loading;		// Variable de tipo Loading para mostrar el ProgressBar cuando la pgina est cargando.
	citas 		= new Array();	// Array con todas las citas futuras del paciente.
		
	bHigienes 	= {name : 'MIS HIGIENES', svg: '', openPage : 'Higiene', class : '', tipo : 'page', gradiente: ''};
	bPedirCita 	= {name : 'PEDIR CITA', svg: '', openPage : 'PedirCita', class : 'active', tipo : 'page', gradiente: ''};
	
	constructor(private app : App, private toastCtrl: ToastController, private domSanitizer: DomSanitizer, public events: Events, private alertCtrl: AlertController, public navCtrl: NavController, public restProvider: RestProvider, private loadingCtrl: LoadingController) {
		this.showLoading();
		this.getCitas();
		this.events.publish("user:logged");
	}

	/**
	* 	Funcin que abre una pgina o una web dependiendo
	*	de los parmetros que se les introduzca.
	*
	* 	@param String page a la que redirigir.
	* 	@param String tipo si es pagina o web.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/  
	openPage(page, tipo) {
		if(tipo === "page"){
			if(page == "Higiene")
				this.app.getRootNav().push(TabHigienesPage);				
			else if(page == "PedirCita")
				this.app.getRootNav().push(PedirCitaPage);			
			else
				this.presentToast("La pgina no est disponible.");			
		}else if(tipo == "web"){
			window.open(page, '_system', 'location=yes');
		}else{
			this.presentToast("La pgina '"+page+"' de tipo '"+tipo+"' no est disponible.");
		}		
	}	

	/**
	* 	Funcin que muestra un Toast con la informacin
	*	referente a la accin del usuario.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	presentToast(txt) {
		let toast = this.toastCtrl.create({
			message: txt,
			duration: 3000,
			position: 'bottom',
			showCloseButton: true,
			closeButtonText: 'OK'
		});
		toast.present();
	}   

	/**
	* 	Funcin que obtiene las citas pasadas del paciente
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
	getCitas(){
		this.restProvider.getCitasPasadas().then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				for (var key in data['data']) {
					this.citas.push(data['data'][key]);
				}
				this.loading.dismiss();
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");		
				this.events.publish("user:Unauthorized");				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");							
			}			
		}).catch(e => {
			this.loading.dismiss();
			console.log(e);
		});
	}	
	
	/**
	* 	Funcin que muestra el ProgressBar cuando alguna accin
	*	se est ejecutando en primer plano.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	showLoading(text = 'Cargando informacin...') {
		this.loading = this.loadingCtrl.create({
			content: text,			
			dismissOnPageChange: false
		});
		this.loading.present();
	}
	
	/**
	* 	Funcin que muestra una alerta con el titulo
	*	y el texto pasado por parmetro.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	showError(title,text) {
		this.loading.dismiss();
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: text,
			buttons: ['OK']
		});
		alert.present();
	}
	
	/**
	* 	Funcin que detecta el movimiento del gesto y pasa
	*	de una pgina a otra.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	swipe(e) {
		if(e.direction == '2'){
			this.navCtrl.parent.select(1);
		}else if(e.direction == '4'){
			this.navCtrl.parent.select(0);
		}else if(e.direction == '1'){
			this.getCitas();
		}
	}	
 
}



