import { Component } from '@angular/core';
import { IonicPage, NavController, Loading, AlertController, LoadingController, NavParams, Events } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';
import { LoginPage } from '../../pages/login/login';
import { PedirCitaPreferenciasPage } from '../../pages/pedir-cita-preferencias/pedir-cita-preferencias';

@IonicPage()
@Component({
  selector: 'page-pedir-cita',
  templateUrl: 'pedir-cita.html',
})

export class PedirCitaPage {	
	
	loading: 			Loading; 	// Variable de tipo Loading para mostrar el ProgressBar cuando la pgina est cargando.
	tratamientos 		= [];		// Array donde se almacenan todos los tratamientos asociados a ese doctor.
	ttoSelect 			= "HIGREC";	// String donde se almacena el tratamiento seleccionado.
		
	bSiguiente 			= {name : 'Siguiente', svg: '', openPage : '', class : 'active login', tipo : '', gradiente: ''};
	tituloSubtitulo 	= {titulo : "Elige el tratamiento", subtitulo: "de la cita"};


	constructor(private alertCtrl: AlertController, public events: Events, private loadingCtrl: LoadingController, public restProvider: RestProvider,public navCtrl: NavController, public navParams: NavParams) {
		this.showLoading();
		this.getTratamientos();
		this.events.publish("user:logged");
	}

	selectTto(e){		

		for (var x in this.tratamientos){
			if(this.tratamientos[x].IdOpc == e.IdOpc)
				this.tratamientos[x].class = "active";
			else
				this.tratamientos[x].class = "";
		}
		this.ttoSelect = e.IdOpc;
	}
  

	siguiente(){
		this.navCtrl.push(PedirCitaPreferenciasPage, {
		  'tto': this.ttoSelect
		});
	}
	
	/**
	* 	Funcin que obtiene todos los tratamientos asignados
	*	al doctor seleccionado.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/	
	getTratamientos(){
		this.restProvider.getTratamientos().then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				this.tratamientos = data['data'];				
				this.loading.dismiss();
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.navCtrl.setRoot(LoginPage);				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");
			}			
		}).catch(e => {
			this.loading.dismiss();
			console.log(e);
		});		
	}
		
	
	/**
	* 	Funcin que enva un E-mail a recepcin para que estas
	*	inserten la cita desde el buscador.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	solicitarCita(fecha, hora, doctor, tratamiento){
		this.showLoading('Solicitando cita ...');
		this.restProvider.solicitarCita(fecha, hora, doctor, tratamiento).then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){					
				this.showError("Atencin!",data['message']);					
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.navCtrl.setRoot(LoginPage);				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");	
			}			
		}).catch(e => {
			this.loading.dismiss();
		});
	}
	
	/**
	* 	Funcin que muestra el ProgressBar cuando alguna accin
	*	se est ejecutando en primer plano.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	showLoading(cont = 'Cargando informacin...') {
		this.loading = this.loadingCtrl.create({
			content: cont
		});
		this.loading.present();
	}
	
	/**
	* 	Funcin que muestra una alerta con el titulo
	*	y el texto pasado por parmetro.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	showError(title,text) {	
		this.loading.dismiss();	
		let alert = this.alertCtrl.create({
			title: title,
			message: text,
			buttons: ['OK']
		});
		alert.present();
	}
}


