import { Component, ViewChild} from '@angular/core';
import { App, NavController, Loading, ToastController, LoadingController, Platform, PopoverController, AlertController, Events  } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';
import { PopoverPage } from '../../pages/popover/popover';
import { Calendar } from '@ionic-native/calendar';

import { TabHigienesPage } from '../tab-higienes/tab-higienes';
import { PedirCitaPage } from '../pedir-cita/pedir-cita';


// Para aceptar HTML desde la API
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'page-list',
	templateUrl: 'consultar-citas-futuras.html'
})

export class ConsultarCitasFuturasPage {

	loading: 	Loading;		// Variable de tipo Loading para mostrar el ProgressBar cuando la pgina est cargando.
	citas 		= new Array();	// Array con todas las citas futuras del paciente.
	calendars 	= [];			// Array con la informacin de la cita para almacenar en el calendario.
	showMessage = false;
	fecha 		= "";			// Fecha que ser obtenida por parmetro
	hora 		= "";			// Hora que ser obtenida por parmetro

	bHigienes 	= {name : 'MIS HIGIENES', svg: '', openPage : 'Higiene', class : '', tipo : 'page', gradiente: ''};
	bPedirCita 	= {name : 'PEDIR CITA', svg: '', openPage : 'PedirCita', class : 'active', tipo : 'page', gradiente: ''};
	
	constructor(private app : App, private toastCtrl: ToastController, private domSanitizer: DomSanitizer, public events: Events, private alertCtrl: AlertController, public popoverCtrl: PopoverController, private calendar: Calendar,  public navCtrl: NavController, public restProvider: RestProvider, private loadingCtrl: LoadingController, private plt: Platform) {
		this.showLoading();
		this.getCitas();
		
		 if (this.plt.is('cordova')) {
			this.plt.ready().then(() => {
				this.calendar.listCalendars().then(data => {
					this.calendars = data;
				});
			})	
		 }
		
		this.events.publish("user:logged");
	}

	/**
	* 	Funcin que abre una pgina o una web dependiendo
	*	de los parmetros que se les introduzca.
	*
	* 	@param String page a la que redirigir.
	* 	@param String tipo si es pagina o web.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/  
	openPage(page, tipo) {
		if(tipo === "page"){
			if(page == "Higiene")
				this.app.getRootNav().push(TabHigienesPage);				
			else if(page == "PedirCita")
				this.app.getRootNav().push(PedirCitaPage);			
			else
				this.presentToast("La pgina no est disponible.");			
		}else if(tipo == "web"){
			window.open(page, '_system', 'location=yes');
		}else{
			this.presentToast("La pgina '"+page+"' de tipo '"+tipo+"' no est disponible.");
		}		
	}

	/**
	* 	Funcin que muestra un Toast con la informacin
	*	referente a la accin del usuario.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	presentToast(txt) {
		let toast = this.toastCtrl.create({
			message: txt,
			duration: 3000,
			position: 'bottom',
			showCloseButton: true,
			closeButtonText: 'OK'
		});
		toast.present();
	}

	/**
	* 	Funcin que muestra una alerta para confirmar o
	*	anular la accin requerida.
	*
	* 	@param String Accion de gestin de la cita (Anulada, Cambio o Confirmada)
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	presentConfirm(action,fechaDecimal, horaDecimal) {
		let alert = this.alertCtrl.create({
			title: 'Confirmacin requerida',
			message: 'Quieres ' + action + ' la cita?',
			buttons: [{ text: 'CANCELAR', role: 'cancel'},{
					text: action,
					handler: () => {
						this.showLoading("Gestionando la cita ...");
						this.gestionarCita(action, fechaDecimal, horaDecimal);							
					}
				}
			]
		});
		alert.present();
	}

	/**
	* 	Funcin que muestra gestiona la cita haciendo
	*	uso de la API del sistema
	*
	* 	@param String Tipo de gestin de la cita (Anulada, Cambio o Confirmada)
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	gestionarCita(tipo, fechaDecimal, horaDecimal) {		
		var textoAlert = "";
		
		if(tipo == "anular")
			textoAlert = "Hemos anulado tu cita.";
		else if(tipo == "cambiar")
			textoAlert = "Nos pondremos en contacto contigo para cambiar la cita.";
		else if(tipo == "confirmar")
			textoAlert = "Hemos confirmado tu cita.";
		
		this.restProvider.gestionarCita(tipo, fechaDecimal, horaDecimal).then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				this.showError("Informacin",textoAlert);	
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.events.publish("user:Unauthorized");				
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");
			}			
		}).catch(e => {
			this.showError("ERROR","Hubo un error al gestionar tu cita.");	
		});		
	}
	
	/**
	* 	Funcin que muestra un pop-up para gestionar la cita.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
	presentPopover(myEvent, fecha, hora) {
		let popover = this.popoverCtrl.create(PopoverPage, {fecha:fecha, hora:hora});
		popover.present({
			ev: myEvent
		});
	}
	
	/**
	* 	Funcin que aade al calendario una cita.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
	addEvent(timestampINI,timestampFIN) {
		
		let dateINI 	= new Date(parseInt(timestampINI));
		let dateFIN 	= new Date(parseInt(timestampFIN));
		let titulo 		= 'Cita en Clnica Dental Ferrus&Bratos';
		let direccion 	= 'C/ Caleruega 67 3 Planta. 28033 Madrid';
		
		let options = { calendarId: 1, url: 'http://clinicaferrusbratos.com', firstReminderMinutes: 15 };
	 
		this.calendar.createEventInteractivelyWithOptions(titulo, direccion, '', dateINI, dateFIN, options)
		.then(
			res => { 
				//this.showError("Bien!", "La cita ha sido aadida al calendario." + res);
			}, 	
			err => {
				this.showError("ERROR", "No ha sido posible aadir la cita al calendario.");
			}
		).catch( 
			e => { 
				this.showError("ERROR", "No ha sido posible aadir la cita al calendario.");
			}
		);
	}
	
	/**
	* 	Funcin que convierte los numeros a dos digitos
	*
	* 	@param Integer Nmero a convertir
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return Nmero de dos digitos 
	*/
	pad(a){
		return (a < 10 ? '0' : '') + a
	}
	
	/**
	* 	Funcin que obtiene las citas futuras del paciente
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/
	getCitas(){
		this.restProvider.getCitasFuturas().then(data => {
			if(typeof data != "undefined" &&  data['status'] == 1){
				if(data['code'] == '105260'){ 
					this.showMessage = true;
					this.citas = data['data'];
				}else{
					for (var key in data['data']) {
						this.citas.push(data['data'][key]);
					}
				}
				this.loading.dismiss();
			}else if(data.status == 401){
				this.showError("Atencin!","Se ha perdido la sesin, por favor vuelva a iniciar.");
				this.events.publish("user:Unauthorized");	
			}else{
				this.showError("Atencin!","<p>" + data['message'] + "<br/><br/>[Code: " + data['code'] + "]</p>");					
			}			
		}).catch(e => {
			this.loading.dismiss();
			console.log(e);
		});		
	}
	
	/**
	* 	Funcin que muestra el ProgressBar cuando alguna accin
	*	se est ejecutando en primer plano.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	showLoading(text = 'Cargando informacin...') {
		this.loading = this.loadingCtrl.create({
			content: text,			
			dismissOnPageChange: false
		});
		this.loading.present();
	}
	
	/**
	* 	Funcin que muestra una alerta con el titulo
	*	y el texto pasado por parmetro.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	showError(title,text) {
		this.loading.dismiss();
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: text,
			buttons: ['OK']
		});
		alert.present();
	}
	
	/**
	* 	Funcin que detecta el movimiento del gesto y pasa
	*	de una pgina a otra.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	swipe(e) {
		if(e.direction == '2'){
			this.app.getRootNav().parent.select(1);
		}else if(e.direction == '4'){
			this.app.getRootNav().parent.select(0);
		}else if(e.direction == '1'){
			this.getCitas();
		}
	}

}



