import { Component } from '@angular/core';
import { RestProvider } from '../../providers/rest/rest';
import { NavController, Loading, ToastController, IonicPage, LoadingController, AlertController, Events  } from 'ionic-angular';
import { HomePage } from '../../pages/home/home';
import { ChangePasswordPage } from '../../pages/change-password/change-password';
import { LoginTabPage } from '../../pages/login-tab/login-tab';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})

export class LoginPage {

	loading: Loading;									// Variable de tipo Loading para mostrar el ProgressBar cuando la pgina est cargando.
	registerCredentials = { email: '', password: '' };	// Array con los campos del formulario
	bCrearCuenta 	= {name : 'Crear cuenta', svg: '', openPage : 'Registro', class : 'active login', tipo : 'page', gradiente: ''};
	bIniciarSesion 	= {name : 'Iniciar sesin', svg: '', openPage : 'Login', class : 'login', tipo : 'page', gradiente: ''};


	constructor(private toastCtrl: ToastController, public events: Events, private nav: NavController, public restProvider: RestProvider,private alertCtrl: AlertController, private loadingCtrl: LoadingController) {
				
		var timeNow = new Date(2100,12,31,23,59,59,0); // Obtengo una fecha en el futuro por si la API no devuelve fecha.
		var expires = new Date(2100,12,31,23,59,59,0); // Obtengo una fecha en el futuro por si la API no devuelve fecha.
		
		// Compruebo si la fecha de expiracin es posterior
		// a la fecha actual del sistema, si es as redirijo
		// a la pgina de home.
		this.restProvider.getTimeServer().then(data => {
			
			if(typeof data != "undefined" && data['status'] == 1){
				timeNow = new Date(Number(data['timeStamp']));
				expires = new Date(Number(data['expires']));
			}
			
			if(expires > timeNow){
				this.events.publish("user:logged");	
				this.nav.setRoot(HomePage);	
			}		
		});
	}

	/**
	* 	Funcin que abre una pgina o una web dependiendo
	*	de los parmetros que se les introduzca.
	*
	* 	@param String page a la que redirigir.
	* 	@param String tipo si es pagina o web.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/  
	openPage(page, tipo) {
		if(tipo === "page"){
			if(page == "Login")
				this.nav.push(LoginTabPage, { pageDefault: "0" } );				
			else if(page == "Registro")
				this.nav.push(LoginTabPage, { pageDefault: "1" });			
			else
				this.presentToast("La pgina no est disponible.");			
		}else if(tipo == "web"){
			window.open(page, '_system', 'location=yes');
		}else{
			this.presentToast("La pgina '"+page+"' de tipo '"+tipo+"' no est disponible.");
		}		
	}

	/**
	* 	Funcin que muestra un Toast con la informacin
	*	referente a la accin del usuario.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	presentToast(txt) {
		let toast = this.toastCtrl.create({
			message: txt,
			duration: 3000,
			position: 'bottom',
			showCloseButton: true,
			closeButtonText: 'OK'
		});
		toast.present();
	}
	
	/**
	* 	Funcin que comprueba si el usuario y la contrasea
	*	son correctos.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 	
	public login() {		
			
		var user = this.registerCredentials.email;
		var pass = this.registerCredentials.password;			
				
		if(user == '' || pass == '')
			return;		
		
		this.showLoading();
		this.restProvider.login(user,pass).then(data => {
			if(typeof data != "undefined" && data['status'] == 1){
				
				window.localStorage.setItem("idPac", data['idPac']);					
				window.localStorage.setItem("token", data['token']);				
				window.localStorage.setItem("expires", data['expires']);

				this.events.publish("user:logged");					
	
				if(data['isDefault'] == 1)
					this.nav.push(ChangePasswordPage, {first: true});
				else
					this.nav.setRoot(HomePage);
				
			}else{
				if(typeof data['code'] != "undefined")
					this.showError("ERROR " + data['code'],"Acceso Denegado");			
				else
					this.showError("ERROR","Acceso Denegado");			
			}
		});
		
	}
	
	/**
	* 	Funcin que muestra el ProgressBar cuando alguna accin
	*	se est ejecutando en primer plano.
	*
	* 	@param None
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	@return None 
	*/ 
	showLoading() {
		this.loading = this.loadingCtrl.create({
			content: 'Cargando informacin...',			
			dismissOnPageChange: true
		});
		this.loading.present();
	}
	
	/**
	* 	Funcin que muestra una alerta con el titulo
	*	y el texto pasado por parmetro.
	*
	* 	@param String Titulo de la alerta.
	* 	@param String Texto de la alerta.
	* 
	* 	@author Jess Ro <jesusriobarrilero@gmail.com>
	* 	
	*/
	showError(title,text) {
		this.loading.dismiss();
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: text,
			buttons: ['OK']
		});
		alert.present();
	}

}



